.product_box {
    position: relative;
    margin-bottom: 30px;
    box-shadow: 0 0 51px rgba(13, 3, 3, 0.13);
    padding: 15px;
}

.product_box figure {
    margin: 0;
}

.product_box figure img {
    width: 100%;
    height: 343px;
}

.product_box:hover img {
    transform: scale(1.1);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.product_box h3 {
    position: absolute;
    bottom: 15px;
    padding: 0;
    right: 50px;
    text-align: right;
    color: #fff;
    font-size: 30px;
    text-transform: uppercase;
    font-weight: 600;
}

.black {
    color: #242323 !important;
}

.padding_left0 {
    padding-left: 0;
}

.padding_right0 {
    padding-right: 0;
}
